header{
  width:100%;
  display: flex;
  height:300px;
  background: url('../../images/background/discuss.jpeg');
  background-size: cover;
  background-attachment: fixed;
  opacity: 0.7;
}
section{
  &.discuss_board{
    position: sticky;
    top: 0;
    width:100%;
    z-index: 1;
    padding: unset;
    background-color: #e0e0e0;
    .container{
      .row{
        .discuss_type{
          text-align: center;
          color: #000;
          text-decoration: none;
          font-size: 18px;
          font-family: serif;
          padding: 1rem 0;
          &:hover, &.active{
            background-color: rgb(151, 148, 150);
            color: #ffffff;
          }
        }
      }
    }
  }
  &.discuss_type{
    padding: 1rem 0 6rem 0;
    .container{
      .row{
        .discuss_title{
          display: flex;
          padding: 1rem 15px;
          border-bottom: 1px solid #555;
          .type{
            font-size: 22px;
            font-family: serif;
            font-weight: 600;
          }
          .add{
            margin-left: auto;
            cursor: pointer;
            >img{
              width: 18px;
            }
          }
        }
        .discuss_post{
          border-bottom: 1px solid rgb(122, 122, 122);
          font-family: serif;
          padding: 0.5rem 15px;
          .title{
            font-size: 20px;
            color: rgb(134, 134, 134);
          }
          .content{
            text-indent: 20px;
            font-size: 16px;
            padding: 10px 0;
            line-height: 1.5;
            &.overflow{
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
          .info{
            display: flex;
            justify-content: flex-end;
            .read_more{
              font-size: 14px;
              margin-right: 10px;
              color: #b6afaf;
              cursor: pointer;
              text-decoration: underline;
              &:hover {
                background-color: rgb(134, 134, 134);
                color: #fff;
              }
            }
            .author{
              font-size: 14px;
              margin-right: 10px;
            }
            .time{
              font-size: 14px;
            }
          }
        }
      }
    }
  }
  &.discuss_post{
    .container{
      .row{
        .discuss_form{
          padding: 1.5rem 0;
          label{
            font-size: 18px;
            font-family: serif;
            color: #a0a0a0;
          }
          input{
            font-family: serif;
            font-size: 16px;
            margin-bottom: 10px;
            width: 100%;
            padding: 8px 8px;
            border-top: 0;
            border-left: 0;
            border-right: 0;
            border-bottom: 1px solid #e0e0e0;
            height: 30px;
          }
          textarea{
            font-family: serif;
            font-size: 16px;
            width: 100%;
            outline: none;
            padding: 8px;
          }
          a{
            margin : 15px auto 15px auto;
            display: flex;
            border: none;
            padding: 5px;
            font-size: 16px;
            border-radius: 4px;
            width: 80px;
            justify-content: center;
            font-family: serif;
            background-color: rgb(122, 122, 122);
            color: #ffffff;
          }
        }
      }
    }
  }
  &.discuss_content{
    .container{
      .row{
        .content{
          background: #f1f1f1;
          padding: 1rem;
          margin: 1rem 0;
          font-family: serif;
          box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
          .title{
            font-size: 24px;
            letter-spacing: 1px;
            color: #a5a2a2;
          }
          .post_content{
            font-size: 18px;
            padding: 20px 0;
            color: #555;
            text-indent: 16px;
          }
          .post_info{
            display: flex;
            justify-content: flex-end;
            font-size: 15px;
            .author{
              margin-right: 10px;
            }
          }
        }
        .message{
          font-family: serif;
          margin: 0.2rem 0 2rem 0;
          .notify{
            font-size: 14px;
            padding: 0.5rem 0;
          }
          textarea{
            width: 100%;
            margin: 10px 0;
            height: 120px;
            font-size: 13px;
            padding: 15px;
          }
          .submit{
            float: right;
            border: none;
            font-size: 12px;
            padding: 8px 10px;
            border-radius: 5px;
            background-color: rgb(151, 144, 145);
            color: #fff;
          }
        }
        .comments{
          .each_comment{
            padding: 1rem;
            margin-bottom: 1rem;
            font-size: 18px;
            font-family: serif;
            background-color: #e0e0e0;
            border-bottom: 1px solid #a0a0a0;
            .comment_content{

            }
            .comment_info{
              font-size: 14px;
              color: #a0a0a0;
              display: flex;
              justify-content: flex-end;
              .comment_owner{
                margin-right: 10px;
              }
            }
          }
        }
      }
    }
  }
}